<template>
  <div>
    <div class="login">
      <LoginPage />
    </div>
    <div class="forgot-password">
      <LoginForgot />
    </div>
  </div>
</template>

<script>
import LoginPage from "../components/login/Loginpage.vue"
import LoginForgot from "../components/login/Loginforgot.vue"
export default {
  components: {
    LoginPage,LoginForgot
  },
  mounted(){
    $('#resetpwd').hide();
    $('#btnresetpwd').click(function () {
        $('#resetpwd').show(1000);
        $('#loginpage').slideUp(1000, function () {
            $('#loginpage').hide();
        });
    });
    $('#back').click(function () { 
      $('#resetpwd').slideUp(1000, function(){
            $('#resetpwd').hide();
        });
        $('#loginpage').show(1000);
    });
  }
};
</script>

<style>
.content {
  padding: 0 !important;
}
body{
  overflow: hidden;
}
</style>