        <h1 class="mb-4 mt-3">RESET PASSWORD</h1>
        <p>
          Ketika anda mengetikkan npm, pastikan nama email yang tertera adalah
          email aktif anda
        </p>
        <form id="forgot-password" @submit.prevent="submit">
          <div class="form-group">
            <input
              type="number"
              class="form-control form-control-lg"
              placeholder="NPM"
              v-model="form.npm"
            />
            <small class="form-text text-muted"></small>
          </div>
          <div class="row">
            <div id="req" class="col-md">
              <button class="btn btn-biru btn-lg w-100" :disabled="isDisabled">
                <div
                  class="spinner-border text-white"
                  style="width: 18px;height:18px"
                  v-if="isDisabled == true"
                >
                  <span class="sr-only">Loading...</span>
                </div>
                Cek
              </button>
            </div>
            <div id="kem" class="col-md">
              <a class="btn btn-outline-hijau btn-lg w-100" id="back"
                >Kembali</a
              >
            </div>
