<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Loginforgot",
  inject: ["notyf"],
  data() {
    return {
      show: [],
      form: {
        npm: null,
      },
      isDisabled: false,
    };
  },
  computed: {
    // ...mapGetters({
    //   getshow: "shownpmemail/showemail",
    // }),
  },
  methods: {
    ...mapActions({
      showEmail: "shownpmemail/showfind",
    }),
    async submit() {
      try {
        await this.showEmail(this.form.npm);
        this.isDisabled = true
        setInterval(() => {
          this.isDisabled = false
        }, 3000);
      } catch (error) {
        this.notyf.error({
          message: "Email Tidak Ada",
          dismissible: false,
          duration: 5000,
        });
      }
    },
  },
  mounted() {
    // this.form.email = this.getshow.email;
    // console.log(this.form.email);
  },
};
</script>

