    <div class="col-md-5 bg-white">
      <div class="row">
        <div class="col-md-10 mr-auto ml-auto">
          <img
            src="@/assets/img/icon/LOGOSTTB.png"
            class="img-fluid mt-5 mb-5"
            width="200px"
          />
          <h1>OASIS</h1>
          <p class="font-weight-bold">
            Selamat Datang kembali, silahkan login ke akun Anda untuk
            melanjutkan
          </p>

          <form id="login-page" class="form" @submit.prevent="submit">
            <div class="form-group">
              <label class="form-label">NPM</label>
              <input
                type="text"
                class="form-control form-control-lg"
                v-bind:class="validate"
                name="npm"
                placeholder="2021XXXXXXX"
                id="username"
                v-model="form.username"
                required
              />

            </div>
            <div class="form-group">
              <label class="form-label">Password</label>
              <input
                type="password"
                class="form-control form-control-lg"
                v-bind:class="validate"
                name="validation-password"
                placeholder="***_***"
                id="password"
                v-model="form.password"
                required
              />
              <div>

              </div>
              
              <div class="form-check pt-2">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="rememberMe"
                  v-model="form.remember_token"
                  @change="rememberMe"
                />
                <label class="form-check-label" for="rememberMe"
                  >Ingat Saya</label
                >
                <a class="float-right link-ungu" id="btnresetpwd"
                >Lupa Password?</a
              >

                
              </div>

              <div id="text_capslock" class="mt-2" style="font-size: 12px">
                <span class="text-muted text-orange-accent font-weight-bold"
                  ><img src="@/assets/img/icon/alert-circle.svg"
                /></span>
                CAPSLOCK sedang aktif
              </div>
            </div>

            <button type="submit" class="btn btn-biru w-100">Masuk</button>
          </form>

          <div class="text-center mt-2">
            <small class="text-center"
              >Perlu Bantuan ?
              <a
                href="https://api.whatsapp.com/send?phone=+6285860607467"
                class="link-ungu"
                >Hubungi Bagian GESIT</a
              ></small
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import axios from "axios";
export default {
  inject: ["notyf"],
  name: "Loginpage",
  data() {
    return {
      form: {
        username: "",
        password: "",
        remember_token: false
      },
      validate: {
        formError: false,
      },
    };
  },
  methods: {
    ...mapActions({
      signIn: "auth/signIn",
    }),
    validating() {
      this.validate.formError = true;
    },
    wasValidating() {
      this.validate.formError = false;
    },
    rememberMe(){
      
    },
    async submit() {
      try {
        
        await this.signIn(this.form);
        window.location.href = "/home";
      } catch (error) {
        this.validating();

        setTimeout(() => {
          this.wasValidating();
        }, 5000);

        this.notyf.error({
          message: "Username / Password Salah",
          dismissible: true,
          duration: 5000,
        });
      }
    },
  },
  mounted() {
    var input = document.getElementById("password");
    var text = document.getElementById("text_capslock");
    text.style.display = "none";
    input.addEventListener("keyup", function (event) {
      if (event.getModifierState("CapsLock")) {
        text.style.display = "block";
      } else {
        text.style.display = "none";
      }
    });
    // (function() {
    //   'use strict';
    //   window.addEventListener('load', function() {
    //     // Get the forms we want to add validation styles to
    //     var forms = document.getElementsByClassName('form');
    //     // Loop over them and prevent submission
    //     var validation = Array.prototype.filter.call(forms, function(form) {

    //       form.addEventListener('submit', function(event) {
    //         if (form.checkValidity() === false) {
    //           event.preventDefault();
    //           event.stopPropagation();
    //         }
    //         form.classList.add('was-validated');
    //       }, false);
    //     });
    //   }, false);
    // })();
  },
};
</script>
